<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card elevation="2">
          <v-card-title>{{pageTitle}}</v-card-title>
          <v-card-text>
            <v-form>
              <v-alert type="error" dark v-if="errorMessage">{{errorMessage}}</v-alert>
              <v-alert type="success" dark v-if="successMessage">{{successMessage}}</v-alert>
              <v-text-field v-model="form.name" :disabled="loading" label="Name" type="text"></v-text-field>
              <v-btn color="primary" @click="submit">{{buttonName}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      errorMessage: null,
      successMessage: null,
      form: {
        project_id: this.$store.state.project.selected.id,
        name: null
      }
    }
  },
  async mounted() {
    if (this.$route.query.id) {
      await this.loadItem();
    }
  },
  computed: {
    buttonName() {
      return this.$route.query.id ? 'Edit' : 'Create';
    },
    pageTitle() {
      return this.$route.query.id ? 'Edit Rest Api' : 'New Rest Api';
    }
  },
  methods: {
    async loadItem() {
      this.loading = true;
      try {
        let response = await axios.get(`/api/rest-apis/${this.$route.query.id}`);
        this.form.name = response.data.rest_api.name;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    },
    async submit() {
      this.loading = true;
      this.successMessage = null;
      this.errorMessage = null;
      try {
        if (this.$route.query.id) {
          await this.update();
        } else {
          await this.create();
        }
      } catch (e) {
        console.log(e);
        this.errorMessage = 'An error occurred!';
      } finally {
        this.loading = false;
      }
    },
    async update() {
      await axios.put(`/api/rest-apis/${this.$route.query.id}`, this.form);
      this.successMessage = 'Rest api has been updated!';
    },
    async create() {
      await axios.post('/api/rest-apis', this.form);
      this.form.name = null;
      this.successMessage = 'Rest api has been created!';
    }
  }
}
</script>
